import http from '@/api/http.js'
import QSPopup from '@/components/QS-popup/QS-popup.vue';
export default{
	components:{QSPopup},
	data(){
		return{
			memberList:[],    //会员商品列表
			phone:''
		}
	},
	onLoad() {
		this.getList()
		this.getPhone()
	},
	onNavigationBarButtonTap() {
		this.show('QSPopup')
	},

	onReachBottom() {
		this.getNewList()
	},
	methods:{
		toDetail(id){
			uni.navigateTo({
				url:'/pages/index/detail?goodsid=' + id+'&member=member'
			})
		},
		show(ref) {
			this.$refs[ref].show();
		},
		hides(ref) {
			this.$refs[ref].hide();
		},
		getPhone(){
			http('/api/Setting/servicePhone','post').then(res=>{
				if(res.data.status == '200'){
					this.phone = res.data.data.service_phone
				}
				console.log(res)
			})
		},
		getList(){
			let data = {
				token:uni.getStorageSync('token'),
				is_special:1
			}
			http('/api/Goods/getMemberGoodsList','post',data).then(res=>{
				if(res.data.status == '200'){
					this.memberList= res.data.data.goodsList.data
				}else{
					this.$util.showToast(res.data.msg)
					return
				}
				console.log(res)
			})
		},
		
		callPhone(){
			let that = this
			uni.showModal({
			    title: '拨打客服',
			    content: '是否确认拨打客服电话',
				cancelColor:'#bbbbbb',
				confirmColor:'#FB4142',
			    success: function (res) {
			        if (res.confirm) {
			            console.log('用户点击确定');
						uni.makePhoneCall({
						 	
						 	// 手机号
						    phoneNumber:that.phone, 
						
							// 成功回调
							success: (res) => {
								console.log('调用成功!')	
							},
						
							// 失败回调
							fail: (res) => {
								console.log('调用失败!')
							}
							
						  })
						
			        } else if (res.cancel) {
			            console.log('用户点击取消');
			        }
			    }
			});
		}
	}
}
